<template>
  <div class="home">
    <div class="left">
      <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
      <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
      <div v-if="resetCanvas"><canvas  id="canvas"></canvas></div>
      <controlpanel></controlpanel>
      <switchs :setting='{choice:1}'></switchs>
      <!-- <div class="img"></div> -->
    </div>
    <div class="right" id="threeId">
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import controlpanel from '@/components/canvas/controlpanel.vue'
import switchs from '@/components/switch.vue'
import {can_init,$canvas} from '@/utils/canvasInit'
import {three_init} from '@/utils/threeInit'
import {mapState, mapGetters, mapMutations} from 'vuex'
export default {
  name: 'Three',
  data(){
    return {
      resetCanvas:true,
      t:0,
      canvas:null,
      canObj:null
    }
  },
  computed:{
    //  `this.name`  `this.$store.state.name`
    ...mapState({
      $canvas: state => state.canvas
    }),
    ...mapState('three',{
      $group: state => state.group
    }),
    ...mapGetters('cans',[
      //  `this.getName`  `this.$store.getters.getName`
      "getTest"
    ])
  },
  components: {
    controlpanel,
    switchs
  },
  mounted(){
  	this.$nextTick(()=>{
      var _this =this
      // can_init()
      can_init({
        width:window.innerWidth/2,
        target:this,
        init({canvas,tis}){
          var time;
          $canvas.on('mouse:down', function (event) {
             time = Date.now()
          })
          $canvas.on('mouse:up', function (event) {
            if((Date.now()-time)>200){
              tis.outImg()
            }
          })
          fabric.Image.fromURL('static/yuanzhuti.png', function(oImg) {
              var scX = 700/oImg.width
              var scY = 700/oImg.height
              oImg.set({
                top:0,
                left:0,
                scaleX: scX,
                scaleY: scY
                // ,
                // selectable:false
              })
              $canvas.add(oImg)
          })
        }
      })
      three_init({
        width:window.innerWidth/2,
        init({scene}){
          scene.children.forEach(v=>{
            if(v.type=="Group"){
              // console.log( _this.setGroup)
              _this.setGroup({group:v})
            }
          })
        }
      })
  	})
  },
  methods:{
    ...mapMutations([
      //  `this.mutationSetName()`  `this.$store.commit('mutationSetName')`
      'initFabric'
    ]),
    ...mapMutations('three',[
      //  `this.mutationSetName()`  `this.$store.commit('mutationSetName')`
      'setGroup'
    ]),
    outImg(){
      // 
      var _this =this;
      var c = $canvas.toJSON();
      $canvas.remove($canvas.getObjects()[0])
      const link = $canvas.toDataURL({
        width: 700,
        height: 700,
        left: 0,
        top: 0,
        format: 'jpg',
      })
      $canvas.loadFromJSON(c);
      // $canvas.getObjects()[0].canvas._objects[0].set('selectable',false)
      // .set('selectable',false)
      // console.log(link)
      // console.log(this.$group)
      if(this.$group){
        let image = new Image()
        image.src = link
        // image.src = require('/static/yuanzhutil.jpg')
        image.onload = function () {
          _this.$group.children[0].material.map.image = image;
          _this.$group.children[0].material.map.needsUpdate=true;
        }
      }
    }
  }
}
</script>
<style lang="less" scoped>
.home {
  width: 100vw;height: 100vh;display: flex;position: relative;
  .left {
    width: 50vw;height: 100vh;
  }
  .right {
    width: 50vw;height: 100vh;
  }
  &:after {
    content:'';display: block;background: #000; width: 2px;height: 100vh;position: absolute;top:0px;left:calc((100vw - 2px)/2);
  }
}
.test {
  position:fixed;top:0px;left:0px;
}
</style>